.image-frame{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 60%;
    margin: auto;
}

.frame-img {
    width: 80px;
    height: 80px;
    object-fit: contain;
    cursor: pointer;
}

.active {
    border: #555555 3px solid;
    border-radius: 5px;
}