.sidebar-back {
    width: 100%;
    height: 100vh;
    position: absolute;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1;
    transition: .5s;
    opacity: 0;
    visibility: hidden;
}

.sidebar-back.visible {
    opacity: 1;
    visibility: visible;
}

.sidebar {
    background-color: #000;
    transition: all 1s;
    max-width: 44vw;
    z-index: 999;
    height: 100% !important;
    display: flex;
    flex-shrink: 0;
}

.sidebar-nav-item {
    padding: 19px;
    position: relative;
    cursor: pointer;
    text-align: center;
    color: #999;
    width: 60%;
}

.sidebar-nav-item p {
    font-size: 12px;
    margin-top: 4px;
    margin-bottom: 0;
    font-weight: bold;
}

.sidebar-nav-item:hover .sidebar-icons {
    background-color: white;
}

.sidebar-nav-item:hover {
    color: white;
}

.sidebar-icons {
    background-color: #999999;
    height: 30px;
    margin-bottom: 6px;
}

.sidebar-icons.download {
    mask: url('../../assets/svgs/download-solid.svg') no-repeat center / contain;
}

.menu-right-part a{
    text-decoration: none;
}

.sidebar-icons.search {
    mask: url('../../assets/svgs/search-solid.svg') no-repeat center / contain;
}

.sidebar-icons.upload {
    mask: url('../../assets/svgs/upload-solid.svg') no-repeat center / contain;
}

.mini-search-icon{
    background-color: #999999;
    mask: url('../../assets/svgs/search-solid.svg') no-repeat center / contain;
    height: 25px;
    width: 25px;
    margin: 0 13px;
}

.sidebar .menu-right-part {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}

.menu-left-part {
    background: #293039;
    transition: all 0.25s;
    width: 30vw;
}

.sidebar-nav-item.open {
    background: #293039;
}

.has-search .form-control {
    border-radius: 3px;
    background: transparent;
    outline: none;
    border: none;
    width: 80%;
    font-size: 17px;
}

.has-search .form-control::placeholder{
    letter-spacing: 2px;
}

.has-search .form-control-feedback {
    position: absolute;
    z-index: 2;
    display: block;
    width: 2.375rem;
    height: 2.375rem;
    line-height: 2.3rem;
    text-align: center;
    pointer-events: none;
    border: none;
    background: transparent;
    color: #aaa;
    top: 6px;
    left: 12px;
}

.card {
    background: none;
}

.card img {
    cursor: pointer;
}

.sidebar-title {
    color: #fff;
    font-weight: bold;
}

.search-panel{
    height: 47px;
    border-radius: 6px;
    display: flex;
    align-items: center;
    background: white;
    position: relative;
}

.search-panel > .input-group-append input{
    background: transparent;
}
